.list-item {
  text-align: left;
  list-style: none;
}

.list-item__container {
  border-top: 1px solid #eaeaea;
  margin: 0 10px;
  padding: 10px 0;
  display: flex;
}

.list-item__container:last-of-type {
  border-bottom: 1px solid #eaeaea;
}

.list-item__user-name {
  color: #1e1e1e;
  margin: 0 0 4px;
  font-size: 15px;
  font-weight: 600;
  line-height: 15px;
}

.list-item__message {
  max-width: 193px;
  color: #999;
  margin: 0;
  font-size: 10px;
  font-weight: 500;
  line-height: 15px;
}

.list-item__message_bold {
  color: #1e1e1e;
}

.list-item__wrap {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.list-item__time {
  white-space: nowrap;
  color: #999;
  align-self: flex-end;
  margin: 0;
  font-size: 9px;
  font-weight: 500;
  line-height: 15px;
}

.list-item__count-message {
  color: #fff;
  background-color: #2e69fd;
  border-radius: 50%;
  align-self: flex-end;
  margin: 0;
  padding: 4px 6px;
  font-size: 11px;
  font-weight: 500;
  line-height: 10px;
  display: none;
}

.list-item__count-message_is-show {
  display: block;
}

